<template>
  <div>
    <section id="wrap">
      <h1 class="userClass"></h1>

      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input type="text" v-model="adApply" auto-complete="off"  placeholder="" size="small"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
            <!--<el-button type="primary" @click="next"></el-button>-->
            <el-button type="primary" @click="Add" size="small"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <!---->
      <el-table
        v-loading="isLoading"
        :data="adApplyList"
        style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="">
              <span>{{ props.row.sm_aa_ID }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.sm_aa_ID }}</span>
            </el-form-item>
            <el-form-item label=" ID">
              <span>{{ props.row.sm_aa_AdviertiseTypeID }}</span>
            </el-form-item>
            <el-form-item label=" ID">
              <span>{{ props.row.sm_aa_AgentID }}</span>
            </el-form-item>
            <el-form-item label="">
              <img :src="item" v-for="item in props.row.sm_aa_Images" width="80" height="50" alt="">
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.sm_aa_Describe }}</span>
            </el-form-item>
            <el-form-item label="ID">
              <span>{{ props.row.sm_aa_ProductID }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.sm_aa_CreateTime }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.sm_aa_PassState | getAdApplyPass}}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.sm_aa_FailedReason }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.sm_aa_Remark }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        prop="sm_aa_Describe">
      </el-table-column>
      <el-table-column
        label=""
        prop="sm_aa_PassState">
        <template slot-scope="props">
          <span>{{props.row.sm_aa_PassState | getAdApplyPass}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        prop="sm_aa_PassTime">
      </el-table-column>
      <el-table-column
        label=""
        prop="sm_aa_FailedReason">
      </el-table-column>
      <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="Update(scope.row.sm_aa_ID)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="Delete(scope.row.sm_aa_ID)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_aa_ID" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_aa_AdviertiseTypeID" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_aa_LocationID" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_aa_AgentID" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <a href="javascript:;" class="file">
              <input type="file" name="" ref="upload" accept="image/*" multiple>
            </a>
            <img src="" alt="" v-lazy="item"  v-show="ImageURL.length" v-for="item in ImageURL" style="width: 100px;height: 100px">
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_aa_Describe" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label="ID:" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_aa_ProductID" placeholder="ID" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-date-picker
              v-model="addOptions.data.sm_aa_CreateTime"
              type="date"
              placeholder="">
            </el-date-picker>
          </el-form-item>
            <el-form-item label=":" :label-width="formLabelWidth">
              <el-switch
                v-model="addOptions.data.sm_aa_PassState"
                active-color="#13ce66"
                inactive-color="#ff4949"
                active-value="0"
                inactive-value="1">
              </el-switch>
            </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-date-picker
              v-model="addOptions.data.sm_aa_PassTime"
              type="date"
              placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_aa_OperateCode" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_aa_FailedReason" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.sm_aa_Remark" placeholder="" type="textarea" ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>
      <!---->
      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateAdApplyObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdApplyObj.sm_aa_ID" placeholder=""  ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdApplyObj.sm_aa_AdviertiseTypeID" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdApplyObj.sm_aa_LocationID" placeholder=""  ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdApplyObj.sm_aa_AgentID" placeholder=""  ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdApplyObj.sm_aa_Image" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdApplyObj.sm_aa_Describe" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label="ID:" :label-width="formLabelWidth">
            <el-input v-model="updateAdApplyObj.sm_aa_ProductID" placeholder="ID"  ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdApplyObj.sm_aa_CreateTime" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdApplyObj.sm_aa_PassState" placeholder=""  ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdApplyObj.sm_aa_PassTime" placeholder=""  ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdApplyObj.sm_aa_OperateCode" placeholder=""  ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdApplyObj.sm_aa_FailedReason" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateAdApplyObj.sm_aa_Remark" placeholder="" type="textarea" ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateAdApplySubmit"> </el-button>
        </div>
      </el-dialog>
    </section>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        // value3: true,
        ImageURL:[],
        isLoading:false,
        total:0,
        formLabelWidth:'120px',
        adApply:'',
        addDialog:false,//
        updateDialog:false,//
        addOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "sm_aa_ID": "",//
            "sm_aa_AdviertiseTypeID": "",//
            "sm_aa_LocationID": "",//
            "sm_aa_AgentID": "",//
            "sm_aa_Image": "",//
            "sm_aa_Describe": "",//
            "sm_aa_ProductID": "",//ID
            "sm_aa_CreateTime": "",//
            "sm_aa_PassState": "1",//
            "sm_aa_PassTime": "",//
            "sm_aa_OperateCode": "",//
            "sm_aa_FailedReason": "",//
            "sm_aa_Remark": "",//
          }
        },//
      }
    },
    computed: mapGetters([
      'adApplyList',
      'adPositionList',
      'partnershipTypeList',
      'proviceList',
      'cityList',
      'updateAdApplyObj'
    ]),
    created(){
      this.initData()
      this.initContent().then(()=>{},err=>{
        this.$notify({
          message: err,
          type: 'error'
        });
      })
    },
    methods: {
      async initContent(){
        //
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "sm_aa_ID": "",//
          "sm_aa_AdviertiseTypeID": "",//
          "sm_aa_LocationID": "",//
          "sm_aa_AgentID": "",//
          "sm_aa_Image": "",//
          "sm_aa_Describe": "",//
          "sm_aa_ProductID": "",//ID
          "sm_aa_CreateTime": "",//
          "sm_aa_PassState": "",//
          "sm_aa_PassTime": "",//
          "sm_aa_OperateCode": "",//
          "sm_aa_FailedReason": "",//
          "sm_aa_Remark": "",//
        };
        await this.$store.dispatch('initPartnershipType',options)
      },
      //
      initData(){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "sm_aa_ID": "",//
          "sm_aa_AdviertiseTypeID": "",//
          "sm_aa_LocationID": "",//
          "sm_aa_AgentID": "",//
          "sm_aa_Image": "",//
          "sm_aa_Describe": "",//
          "sm_aa_ProductID": "",//ID
          "sm_aa_CreateTime": "",//
          "sm_aa_PassState": "",//
          "sm_aa_PassTime": "",//
          "sm_aa_OperateCode": "",//
          "sm_aa_FailedReason": "",//
          "sm_aa_Remark": "",//
        }
        this.isLoading = true;
        // console.log("options:"+options)
        this.$store.dispatch('initAdApply',options)
          .then(total=>{
            this.total = total;
            this.isLoading = false;
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      search(){
        this.initData()
      },
      //
      Add(){
        let obj =  this.addOptions.data
        for(var attr in obj ){
          obj[attr] = ''  //      obj.attr
        }
        this.addOptions.data = obj;
        this.uploaNode();
        this.addDialog = true;
        this.$store.commit('setTranstionFalse');
      },
      //
      uploadImg(file) {
        return new Promise(function (relove, reject) {
          lrz(file)
            .then(data => {
              relove(data.base64.split(',')[1])
            })
        })
      },
      uploaNode() {
        this.ImageURL = [];
        setTimeout(() => {
          if (this.$refs.upload) {
            this.$refs.upload.addEventListener('change', data => {
              for (var i = 0; i < this.$refs.upload.files.length; i++) {
                this.uploadImg(this.$refs.upload.files[i]).then(data => {
                  this.$store.dispatch('adApplyUploadAdminImgs', {
                    imageData: data
                  })
                    .then(data => {
                      if (data) {
                        this.ImageURL.push(data.data);
                      } else {
                        this.$notify({
                          message: '!',
                          type: 'error'
                        });
                      }
                    })
                })
              }
            })
          }
        }, 30)
      },
      //
      addSubmit(){
        //
        this.addOptions.data.sm_aa_Image = this.ImageURL.join(',');
        // console.log(this.ImageURL);
        // return;
        this.$store.dispatch('AddAdApply',this.addOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });

            this.initData()
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.addDialog = false;
      },
      //
      Update(id){
        this.updateDialog = true;
        this.$store.commit('setTranstionFalse');
        this.$store.commit('initUpdateAdApply',id)
      },
      //
      updateAdApplySubmit(){
        let updateOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateAdApplyObj
        };
        this.$store.dispatch('UpdateAdApplyObj',updateOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.adApply)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.updateDialog = false
      },
      //
      Delete(id){
        let deleteOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "sm_aa_ID": id,//
          }
        };
        this.$store.dispatch('DeleteAdApply',deleteOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.adApply)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      }
    },
  }
</script>
<style scoped>

</style>



